                class="text-secondary bg-accent q-py-xs q-px-sm rounded font-medium"
              />
            </q-breadcrumbs>
            <div class="q-pt-md q-pb-sm" id="overview-title">
              <div class="row items-center q-gutter-sm">
                <span class="text-h4 font-demi-bold text-primary">
                  {{ taskInfo.title }}
                </span>
                <q-badge
