                  @click="$router.push(`/brands/${brandId}`)"
                >
                  {{ brandInfo.brand_name }}
                </q-badge>
              </div>
            </div>
          </div>
        </div>
      </q-card>

      <!-- Body -->

      <q-card flat>
        <q-splitter
          v-model="splitterModel"
          unit="px"
          id="mainSplitter"
          ref="mainSplitter"
          :separator-class="`bg-transparent q-mx-md${
            $q.screen.lt.sm ? ' hidden' : ''
          }`"
          :before-class="`sticky sticky-menu-top${
            $q.screen.lt.sm ? ' hidden' : ''
          }`"
        >
          <template v-slot:before>
            <div class="q-gutter-sm">
              <q-card
                flat
                v-ripple="{ color: 'secondary' }"
                v-for="(tab, i) in allSections"
                :key="tab.key"
                class="cursor-pointer rounded hover-text-secondary hover-bg-accent text-body1 font-medium"
                :class="
                  (tab.key === tabSectionId && tabSectionIdScrolled === '') ||
                  tabSectionIdScrolled === tab.key ||
                  (tabSectionIdScrolled === '' &&
                    tabSectionId === '' &&
                    i === 0)
                    ? 'bg-accent text-secondary'
                    : 'text-primary-500'
                "
                @click="clickScrollEvent(tab.key, false)"
              >
                <q-btn
                  flat
                  no-caps
                  :ripple="false"
                  class="rounded hover-text-secondary hover-bg-accent font-medium button-md"
                >
                  <div class="full-width text-left text-body1">
                    {{ tab.label }}
                  </div>
                </q-btn>
              </q-card>
            </div>
          </template>

          <template v-slot:after>
            <q-card flat class="text-body1 q-pb-xl">
              <q-card-section class="q-pa-none font-medium text-h5">
                <div class="row justify-end items-center">
                  Task Overview

                  <q-space />

                  <div v-if="!editing" class="q-gutter-sm">
                    <q-btn
                      v-if="documentReady"
                      flat
                      round
                      size="sm"
                      icon="icon-printer"
                      class="text-primary-500 hover-text-secondary"
                      :disable="!documentReady || convertingPDF"
                      @click="
                        exportToPDF(
                          `TASK-${taskInfo?.title}-${brandInfo.brand_name}`,
                          ['overview-title', ...overviewIds]
                        )
                      "
                    >
                      <tooltip-pro
                        :anchor="`${
                          $q.screen.lt.md ? 'center left' : 'bottom middle'
                        }`"
                        :self="`${
                          $q.screen.lt.md ? 'center right' : 'top middle'
                        }`"
                      >
                        Export to PDF
                      </tooltip-pro>
                    </q-btn>
                    <q-btn
                      flat
                      round
                      size="sm"
                      icon="icon-edit-05"
                      class="text-primary-500 hover-text-secondary"
                      :disable="!documentReady || convertingPDF"
                      @click="editing = true"
                    >
                      <tooltip-pro>Edit</tooltip-pro>
                    </q-btn>
                  </div>
                  <div v-else class="q-gutter-sm">
                    <q-btn
                      no-caps
                      outline
                      size="sm"
                      class="rounded not-gm hover-negative"
                      @click.stop="editing = false"
                    >
                      Cancel
                    </q-btn>
                    <q-btn
                      no-caps
                      unelevated
                      size="sm"
                      color="primary"
                      class="rounded not-gm hover-bg-secondary"
                      @click.stop="editing = false"
                    >
                      <q-icon name="icon-save-01" size="xs" />
                      <span class="q-pl-xs">Save Changes</span>
                    </q-btn>
                  </div>
                </div>
              </q-card-section>
              <template v-for="(data, i) in overviewData" :key="data">
                <!-- temp-warning -->
                <q-card-section
                  v-if="data.title.toLowerCase() == 'warning'"
                  :id="overviewIds[i]"
                  class="q-px-none q-pb-md"
                >
                  <q-card flat class="rounded bg-color q-pa-md">
                    <div
                      class="target-clickable-id full-width"
                      @click="
                        () =>
                          noTitleClick ? void 0 : clickScrollEvent('warning')
                      "
                    >
                      <div
                        class="items-center flex text-negative font-medium q-gutter-x-sm"
                      >
                        <q-icon name="icon-alert-circle" class="font-medium" />
                        <span>{{ data.title }}</span>
                      </div>
                    </div>
                    <div v-if="!editing" class="q-pt-sm text-primary-500">
                      {{ data.answer[0].value }}
                    </div>
                    <div v-else class="q-pt-sm">
                      <q-input
                        dense
                        outlined
                        type="textarea"
                        class="rounded"
                        v-model="data.answer[0].value"
                      />
                    </div>
                  </q-card>
                </q-card-section>
                <!-- textarea1 -->
                <q-card-section
                  v-if="['textarea1'].includes(data.type)"
                  :id="overviewIds[i]"
                  class="q-px-none"
                >
                  <div
                    class="q-pb-md font-medium text-subtitle1 target-clickable-id"
                    @click="
                      () =>
                        noTitleClick
                          ? void 0
                          : clickScrollEvent('target_audience')
                    "
                  >
                    {{ data.title }}
                  </div>
                  <div v-if="!editing">
                    <template
                      v-for="sentence in data.answer[0].value.split('\n')"
                      :key="sentence"
                    >
                      <p>
                        {{ sentence }}
                      </p>
                    </template>
                  </div>
                  <div v-else>
                    <q-input
                      dense
                      outlined
                      type="textarea"
                      class="rounded"
                      v-model="data.answer[0].value"
                    />
                  </div>
                </q-card-section>
                <!-- textarea2 -->
                <q-card-section
                  v-if="['textarea2'].includes(data.type)"
                  :id="overviewIds[i]"
                  class="q-px-none hover-visibility"
                >
                  <div
                    class="q-pb-md font-medium text-subtitle1 target-clickable-id"
                    @click="
                      () =>
                        noTitleClick ? void 0 : clickScrollEvent('description')
                    "
                  >
                    {{ data.title }}
                  </div>
                  <div v-if="!editing">
                    <div class="raw-html" v-html="data.answer[0].value"></div>
                  </div>
                  <div v-else>
                    <q-editor
                      ref="descriptionEditorRef"
                      @paste="
                        (e) => pastePlainText(e, $refs['descriptionEditorRef'])
                      "
                      v-model="data.answer[0].value"
                      :placeholder="data.placeholder"
                      height="250px"
                      :toolbar="[
                        [
                          {
                            icon: $q.iconSet.editor.align,
                            fixedLabel: true,
                            list: 'only-icons',
                            options: ['left', 'center', 'right', 'justify'],
                          },
                        ],
                        [
                          {
                            label: $q.lang.editor.formatting,
                            icon: $q.iconSet.editor.formatting,
                            list: 'no-icons',
                            options: ['p', 'h5', 'h6', 'code'],
                          },
                        ],
                        ['bold', 'italic', 'underline'],
                        ['link', 'quote'],
                        ['unordered', 'ordered', 'outdent', 'indent'],
                        ['undo', 'redo'],
                      ]"
                    />
                  </div>
                </q-card-section>
                <!-- video_walkthrough -->
                <q-card-section
                  v-if="['video_walkthrough'].includes(data.type)"
                  :id="overviewIds[i]"
                  class="q-px-none"
                >
                  <div
                    class="q-pb-md font-medium text-subtitle1 target-clickable-id"
                    @click="
                      () =>
                        noTitleClick
                          ? void 0
                          : clickScrollEvent('video_walkthrough')
                    "
                  >
                    {{ data.title }}
                  </div>

                  <div v-if="!editing">
                    <q-video
                      :ratio="16 / 9"
                      class="rounded fit"
                      :title="data.title"
                      :src="loomURLShare2Embed(data.answer[0].value)"
                    />
                  </div>
                  <div v-else>
                    <q-input
                      dense
                      outlined
                      class="rounded"
                      v-model="data.answer[0].value"
                    />
                  </div>
                </q-card-section>
                <!-- upload_single | upload_multiple -->
                <q-card-section
                  v-if="
                    ['upload_single', 'upload_multiple'].includes(data.type)
                  "
                  :id="overviewIds[i]"
                  class="q-px-none"
                >
                  <div
                    class="q-pb-md font-medium text-subtitle1 target-clickable-id"
                    @click="
                      () =>
                        noTitleClick ? void 0 : clickScrollEvent('resources')
                    "
                  >
                    {{ data.title }}
                  </div>
                  <template v-if="!editing">
                    <div class="row items-center justify-start q-col-gutter-md">
                      <div
                        v-for="i in 4"
                        :key="i"
                        :ratio="1"
                        class="rounded overflow-hidden col-12 col-sm-6"
                      >
                        <q-img
                          loading="lazy"
                          :src="`${$cdn_host}${$cdn.tmp}/Image-${i}.png`"
                          class="rounded fit with-lightbox"
                          spinner-color="secondary"
                          :alt="`Image-${i}`"
                        />
                      </div>
                    </div>
                  </template>
                  <div v-else>Edit / Upload Files</div>
                </q-card-section>
              </template>
            </q-card>
          </template>
        </q-splitter>
      </q-card>
    </q-card>
    <q-scroll-observer @scroll="scrollHandler" />
    <image-lightbox
      :visible="showLightBox"
      :imgs="lightboxImages"
      :index="lightboxIndex"
      @hide="showLightBox = false"
    />
  </q-page>
</template>

<script>
import { defineComponent } from 'vue';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import { copyToClipboard, debounce, format } from 'quasar';
import {
  avatarNameAbbr,
  loomURLShare2Embed,
} from 'src/assets/scripts/functions';
const { capitalize } = format,
  getEl = (id) => {
    return document.getElementById(id);
  };
import FilesUploader from 'src/components/Helpers/FilesUploader.vue';
import UploadSlot from 'src/components/Helpers/Slots/UploadSlot.vue';
import {
  FileUploader_Mixin,
  Editor_Mixin,
  Export_Task_Project_Mixin,
  Helpers_Mixin,
} from 'src/mixins';

export default defineComponent({
  name: 'TaskOverview',

  mixins: [
    FileUploader_Mixin,
    Editor_Mixin,
    Export_Task_Project_Mixin,
    Helpers_Mixin,
  ],

  // components: { FilesUploader, UploadSlot },

  data() {
    return {
      title: 'Task Overview',
      showLightBox: false,
      lightboxIndex: 0,
      lightboxImages: [],
      tabSectionId: '',
      tabSectionIdScrolled: '',
      splitterModel: 175,
      editing: false,
      noTitleClick: false,
      documentReady: false,
      capitalize,
      avatarNameAbbr,
      loomURLShare2Embed,
      scrollHandler: debounce(this.scrollHandlerOrig, 100),
      taskInfo: {},
    };
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
    const response = await this.getSingleTask(this.taskId);
    console.warn('taskInfo', this.taskId, response);
    if (response?.success) this.taskInfo = response.data;
    console.warn('taskInfo', this.taskId, this.taskInfo);
    this.tabSectionId = this.hashId;
    this.updateLightBox();
    this.$nextTick(() => {
      setTimeout(() => {
        this.documentReady = true;
      }, 1000);
    });
  },

  computed: {
    ...mapState(useUser, ['selectedOrgBrands', 'selectedOrgTasks']),

    hashId() {
      return this.$route.hash.slice(1);
    },

    brandId() {
      return this.$route.params?.brandId;
    },

    taskId() {
      return this.$route.params?.taskId;
    },

    brandInfo() {
      return this.selectedOrgBrands[this.brandId] || {};
    },

    overviewData() {
      return this.taskInfo?.dynamic_questions?.filter?.(
        (e) => e.show_in_overview
      );
    },

    overviewIds() {
      return this.overviewData.map((e) => this.convertToKey(e.title));
    },

    allSections() {
      return this.overviewData.map((e, i) => ({
        label: e.title,
        key: this.overviewIds[i],
      }));
    },
  },

  watch: {
    editing: {
      async handler(val) {
        setTimeout(this.updateLightBox, 500);
      },
    },
    tabSectionId: {
      async handler(val) {
        if (val) {
          // this.$router.push({ hash: '#' + val });
          this.scrollToTarget(val);
          this.tabSectionId = '';
          // this.tabSectionIdScrolled = '';
        }
      },
    },
  },

  methods: {
    ...mapActions(useGrowmodo, ['getSingleTask']),

    updateLightBox() {
      this.lightboxImages = [];
      const lightboxElems = document.querySelectorAll('.with-lightbox');
      lightboxElems.forEach((e, i) => {
        let img = e.querySelector('img');
        this.lightboxImages.push({
          src: img.src,
          title: img.title,
          alt: img.alt,
        });
        e.onclick = () => {
          this.lightboxIndex = i;
          this.showLightBox = true;
        };
      });
    },

    scrollToTarget(targetId) {
      getEl(targetId)?.scrollIntoView?.({
        block: 'start',
        inline: 'nearest',
        behavior: 'smooth',
      });
    },

    clickScrollEvent(targetId, copy = true) {
      this.tabSectionId = targetId;
      // Copy URL to Clipboard
      if (copy) {
        copyToClipboard(
          window.location.origin + this.$route.fullPath + '#' + targetId
        );
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'Anchor has been copied to clipboard.',
          timeout: 500,
        });
      }
    },

    scrollHandlerOrig(scroll) {
      this.tabSectionIdScrolled = '';
      let selectedKey = '';
      this.allSections
        .map((e) => e.key)
        .reverse()
        .forEach((key) => {
          const el = getEl(key),
            offsetTop = el?.offsetTop + 25,
            top = scroll?.position?.top;
          if (top > offsetTop && !selectedKey) {
            selectedKey = key;
          }
        });
      this.tabSectionIdScrolled = selectedKey;
    },
  },
});
</script>
